import { Component, OnInit } from '@angular/core';
import { ProductService } from '../../../../../../auth/_services/product.service';
import { QuickSidebarComponent } from '../../../../../layouts/quick-sidebar/quick-sidebar.component';
import { SideBarService } from '../../../../../layouts/side-bar/side-bar.service';
import { SideBarToggleComponent } from '../../../../../layouts/side-bar-toggle/side-bar-toggle.component';
import { DataService } from '../../../../../../_services/data.service';
import { ToastrService } from 'ngx-toastr';

@Component({
    selector: 'shareable-product',
    templateUrl: "shareable-product.component.html",
    styles: []
})
export class ShareableProductComponent implements OnInit {


    private formSumitAttempt: boolean;

    constructor(private toastr: ToastrService, private _productService: ProductService, private _dataService: DataService) { }
    public shareableProductData;
    public searchValue = "";
    public dropDownValue = '';
    public noOfPage = '';
    public user: any;
    p: number = 1;
    ngOnInit() {
        let loggedUserId = JSON.parse(localStorage.getItem('currentUser'));
        console.log('loggedUserId', loggedUserId.success.id);
        this.user = loggedUserId.success.id;
        this._dataService.setactiveMenu('shareable-product');
        this._productService.shareableList({ user_id: this.user }).subscribe(
            data => {
                this.shareableProductData = data.success;
            }
        )

    }

    addShareableProduct(user_id, product_id) {

        this._productService.addShareableProduct({ user_id: user_id, product_id: product_id }).subscribe(
            data => {
                this.shareableProductData = data.success;
                this.toastr.success('Product Added Successfully', 'Success Message');
            }
        );

    }
}
