import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { DefaultComponent } from '../../../../default/default.component';
import { RouterModule, Routes } from '@angular/router';
import { LayoutModule } from '../../../../../layouts/layout.module';
import { FormsModule } from '@angular/forms';
import { NgxPaginationModule } from 'ngx-pagination';
import { ShareableProductComponent } from './shareable-product.component';
const routes: Routes = [
    {
        path: "",
        component: DefaultComponent,
        children: [
            {
                path: "",
                component: ShareableProductComponent
            }
        ]
    },
];

@NgModule({
    imports: [
        CommonModule, RouterModule.forChild(routes),
        LayoutModule, FormsModule, NgxPaginationModule,
    ],
    declarations: [ShareableProductComponent],
    // providers:[DataService]
})
export class ShareableProductModule { }
